<template>
  <div>
    <PageQatarPreHeader/>
    <div class="pb-6" style="background-color: #020f2a; color: white;">
      <v-container>
        <h3>경기 일정</h3>
        <v-card>
          <v-tabs
              v-model="tab"
              centered
              center-active
              dark
              show-arrows
              background-color="#024e94"
          >
            <v-tab
                v-for="i in groups"
                :key="i"
                :href="i.link">
              {{ i.name }}
            </v-tab>
          </v-tabs>

          <v-tabs-items v-model="tab" touchless>
            <v-tab-item
                v-for="i in 7"
                :key="i"
                :value="'tab-' + i"
            >
              <v-card flat>
                <v-card-text>{{ text }}</v-card-text>
              </v-card>
            </v-tab-item>
            <v-tab-item value="tab-8">
              <v-card flat>
                <template>
                  <v-simple-table class="text-center px-2 px-sm-10">
                    <template v-slot:default>
                      <thead>
                      <tr>
                        <th
                            v-for="index in headers"
                            :key="index"
                            class="text-center"
                        >
                          {{ index }}
                        </th>
                      </tr>
                      </thead>
                      <tbody>
                      <tr
                          v-for="(item, index) in schedules"
                          :key="index"
                      >
                        <td>{{ Math.ceil(item.Home_Winrate * 100) }}%</td>
                        <td>{{ item.Home }}</td>
                        <td>{{ item.Date }}</td>
                        <td>{{ item.Away }}</td>
                        <td>{{ Math.ceil(item.Away_Win_rate * 100) }}%</td>
                        <td v-if="item.Home_Winrate > 0.5">{{ item.Home }}</td>
                        <td v-else-if="item.Home_Winrate == 0.5">Draw</td>
                        <td v-else-if="item.Home_Winrate < 0.5">{{ item.Away }}</td>
                        <td v-else>Thinking..</td>
                      </tr>
                      </tbody>
                    </template>
                  </v-simple-table>
                </template>
              </v-card>
            </v-tab-item>
          </v-tabs-items>
        </v-card>
      </v-container>
    </div>
  </div>
</template>

<script>
import PageQatarPreHeader from "@/components/layout/PageQatarPreHeader";
import http from "@/http-common";

export default {
  name: 'PageQatarPre',

  components: {
    PageQatarPreHeader,
  },

  data () {
    return {
      tab: null,
      text: 'Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat.',
      schedules: [],
      headers: [
        "Rate", "Home", "Date", "Away", "Rate", "Prediction"
      ],
      groups: [
        {link: "#tab-1", name: "A조"},
        {link: "#tab-2", name: "B조"},
        {link: "#tab-3", name: "C조"},
        {link: "#tab-4", name: "D조"},
        {link: "#tab-5", name: "E조"},
        {link: "#tab-6", name: "F조"},
        {link: "#tab-7", name: "G조"},
        {link: "#tab-8", name: "H조"},
      ],
    }
  },

  methods:{
    dataSchedule(){
      http
          .get("/qatar/qpre/schedule")
          .then(response=>{
            this.schedules = response.data;
            console.log(response.data);
          })
          .catch(e=>{
            console.log(e);
          })
    }
  },

  mounted(){
    this.dataSchedule();
  }
}
</script>

